:root {
  --primary: white;
  --secondary: #00a5ff;
  --secondary-light: #b3e4ff;
  --tertiary: #686868;
  --input-text: #a9a9a9;
  --input-border: #eaeaea;
  --beer-names: #505050;
  --delete: #b12723; }

.EditButton,
.DeleteButton {
  width: 5rem;
  height: 5rem;
  padding: 1rem;
  margin: 1rem 1rem 1rem 0;
  background: white;
  color: black;
  border: none;
  border-radius: 10rem;
  font-size: 2rem;
  cursor: pointer;
  transition: 0.2s; }
  @media (max-width: 600px) {
    .EditButton,
    .DeleteButton {
      font-size: 1.5rem;
      width: 3.5rem;
      height: 3.5rem;
      padding: 1rem; } }

.EditButton:hover {
  background: var(--secondary);
  color: white;
  transition: 0.2s; }
  @media (max-width: 600px) {
    .EditButton:hover {
      background: none;
      color: black; } }

.DeleteButton:hover {
  background: var(--delete);
  color: white;
  transition: 0.2s; }
  @media (max-width: 600px) {
    .DeleteButton:hover {
      background: none;
      color: black; } }
