:root {
  --primary: white;
  --secondary: #00a5ff;
  --secondary-light: #b3e4ff;
  --tertiary: #686868;
  --input-text: #a9a9a9;
  --input-border: #eaeaea;
  --beer-names: #505050;
  --delete: #b12723; }

.Container {
  width: 100%;
  max-width: 400px;
  height: auto;
  margin: 0 auto;
  background: white;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23); }

.Title {
  background: black;
  color: white;
  text-align: left;
  padding-top: 4rem;
  padding-bottom: 0.5rem;
  padding-left: 2rem;
  border-top: 2px solid black; }
  .Title h1 {
    font-size: 4rem;
    font-family: 'Montserrat', sans-serif; }
  .Title h2 {
    font-size: 1.6rem;
    position: relative;
    top: 0.5rem; }

.LoginContainer {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding-left: 2rem;
  padding-right: 2rem;
  padding-top: 2rem;
  transition: 0.2s; }

.Group {
  position: relative;
  margin-bottom: 2rem;
  width: 100%; }

.Input {
  font-size: 1.6rem;
  padding: 2rem 1rem 1rem 1.5rem;
  display: block;
  width: 100%;
  border: none;
  background: var(--primary);
  border-bottom: 1px solid var(--input-color);
  -webkit-appearance: none;
  -webkit-border-radius: 0.5rem;
  border: 2px solid var(--input-border); }
  .Input:focus {
    outline: none;
    border: 2px solid var(--secondary); }

/* Label */
.Label {
  color: var(--input-color);
  font-size: 1.6rem;
  font-weight: normal;
  padding-left: 1.5rem;
  position: absolute;
  pointer-events: none;
  top: 2rem;
  transition: 0.2s;
  z-index: 100; }

.Input:focus ~ .Label,
.Input:valid ~ .Label {
  top: -0.5rem;
  margin-left: 1rem;
  padding-left: 0.5rem;
  padding-right: 0.5rem;
  font-size: 1.2rem;
  color: var(--secondary);
  background: var(--primary); }

.ButtonContainer {
  display: flex;
  justify-content: space-between;
  width: 100%; }

.Button {
  padding: 1rem;
  margin-bottom: 2rem;
  width: 45%;
  text-align: center;
  background: black;
  color: white;
  border: none;
  font-size: 2rem;
  transition: 0.2s; }
  .Button:nth-child(1) {
    background: black;
    color: white; }
    .Button:nth-child(1):hover {
      background: var(--tertiary); }
  .Button:nth-child(2) {
    background: var(--secondary);
    color: white; }
    .Button:nth-child(2):hover {
      background-color: var(--secondary-light);
      color: white; }

.Back {
  margin-top: -0.5rem;
  margin-bottom: 1rem;
  width: 100%;
  text-align: left;
  background: none;
  color: black;
  border: none;
  font-size: 4rem;
  transition: 0.2s; }

.Back:hover {
  background: white;
  color: #ccc;
  cursor: pointer;
  transition: 0.2s; }

.Button:hover {
  background: white;
  color: #ccc;
  cursor: pointer;
  transition: 0.2s; }

/* Errors */
.Error {
  height: 5rem;
  font-size: 1.5rem;
  color: red;
  text-align: center;
  vertical-align: middle; }
